@use '../custom' as *;
.carousel-item {
    width: 100%;
    height: 55vh;
    min-height: 200px;
    background: no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    position: absolute;
}

.carousel-item>img {
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 55vh;
    margin: 0 auto;
    min-width: 100%;
    display: block;
}

.carousel-control {
    margin-top: 20px;
}

.carousel-item:before {
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #3D0C02;
    display: block;
    position: absolute;
    opacity: 0.5;
}

@media screen and (max-width: 1170px) {
    .carousel-item {
        width: 100%;
        height: 50vh;
    }
}

@media screen and (max-width: 700px) {
    .carousel-item {
        width: 100%;
        height: 47vh;
    }
}

@media screen and (max-width: 568px) {
    .carousel-item {
        width: 100%;
        height: 47vh;
    }
}